/* Общий контейнер страницы сферы */
.sphere-page {
    min-height: 100vh;
    background: linear-gradient(180deg, #0f0f23 0%, #000 100%);
    position: relative;
    padding: 2rem 1.5rem;
    overflow: hidden;
}

/* Фоновая "аврора" (градиентные пятна) */
.bg-aurora {
    position: fixed;
    inset: 0;
    background:
            radial-gradient(circle at 20% 10%, rgba(139, 92, 246, 0.2) 0%, transparent 50%),
            radial-gradient(circle at 80% 80%, rgba(139, 92, 246, 0.1) 0%, transparent 50%);
    animation: aurora 10s ease-in-out infinite alternate;
    z-index: 0;
}

@keyframes aurora {
    0% { opacity: 0.5; }
    100% { opacity: 0.8; }
}

/* Background pixels effect */
.background-pixels {
    position: absolute;
    inset: 0;
    pointer-events: none;
    z-index: 0;
}

.pixel {
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 0;
    opacity: 0.9;
    animation: pixel-fall 9s linear infinite;
    image-rendering: pixelated;
}

.pixel-variant-1 { top: -28px; left: 6%; background: #8b5cf6; animation-delay: 0.2s; }
.pixel-variant-2 { top: -52px; left: 16%; background: #a78bfa; animation-delay: 1.6s; }
.pixel-variant-3 { top: -33px; left: 36%; background: #c4b5fd; animation-delay: 2.3s; }
.pixel-variant-4 { top: -47px; left: 56%; background: #ddd6fe; animation-delay: 4.1s; }
.pixel-variant-5 { top: -38px; left: 76%; background: #e9d5ff; animation-delay: 4.8s; }
.pixel-variant-6 { top: -58px; left: 86%; background: #f3f4f6; animation-delay: 5.5s; }
.pixel-variant-7 { top: -23px; left: 26%; background: #c4b5fd; animation-delay: 6.9s; }
.pixel-variant-8 { top: -43px; left: 46%; background: #a78bfa; animation-delay: 7.7s; }
.pixel-variant-9 { top: -53px; left: 66%; background: #ddd6fe; animation-delay: 8.4s; }
.pixel-variant-10 { top: -18px; left: 96%; background: #8b5cf6; animation-delay: 9.3s; }

@keyframes pixel-fall {
    0% { transform: translateY(-10vh); opacity: 0; }
    20% { opacity: 1; }
    80% { opacity: 1; }
    100% { transform: translateY(110vh); opacity: 0; }
}

/* Хедер с кнопкой назад и карточкой заголовка */
.header-simple {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 2rem;
    z-index: 2;
    position: relative;
}

/* Круглая кнопка "назад" */
.back-btn {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    color: white;
    font-size: 1.2rem;
    cursor: pointer;
    backdrop-filter: blur(20px);
    transition: all 0.3s ease;
}

.back-btn:hover {
    background: rgba(139, 92, 246, 0.3);
    transform: scale(1.1);
    box-shadow: 0 8px 24px rgba(139, 92, 246, 0.4);
}

/* Карточка с иконкой и заголовком сферы */
.hero-compact {
    flex: 1;
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1.5rem 2rem;
    background: rgba(255, 255, 255, 0.08);
    border-radius: 2rem;
    backdrop-filter: blur(24px);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.hero-icon {
    font-size: 2.5rem;
    filter: drop-shadow(0 4px 12px rgba(139, 92, 246, 0.4));
}

.hero-compact h1 {
    font-size: 2rem;
    font-weight: 800;
    background: linear-gradient(135deg, #8b5cf6, #ffffff);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    margin: 0;
}

/* Основной контейнер контента - УВЕЛИЧЕННАЯ ШИРИНА */
.main-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    max-width: 700px;
    margin: 0 auto;
    z-index: 2;
    position: relative;
    gap: 4rem;
}

/* Обертка для блока со списком задач */
.tasks-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    max-height: 70vh;
    overflow: hidden;
    scrollbar-width: thin;
    scrollbar-color: rgba(139, 92, 246, 0.3) transparent;
}

.tasks-container::-webkit-scrollbar {
    width: 6px;
}

.tasks-container::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 3px;
}

.tasks-container::-webkit-scrollbar-thumb {
    background: rgba(139, 92, 246, 0.4);
    border-radius: 3px;
    transition: background 0.2s ease;
}

.tasks-container::-webkit-scrollbar-thumb:hover {
    background: rgba(139, 92, 246, 0.6);
}

/* Область, в которой скроллится именно список задач */
.tasks-scrollable {
    flex: 1;
    overflow-y: auto;
    padding-right: 0.25rem;
    scrollbar-width: thin;
    scrollbar-color: rgba(139, 92, 246, 0.3) transparent;
}

.tasks-scrollable::-webkit-scrollbar {
    width: 6px;
}

.tasks-scrollable::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.02);
    border-radius: 3px;
    margin: 0.5rem 0;
}

.tasks-scrollable::-webkit-scrollbar-thumb {
    background: rgba(139, 92, 246, 0.4);
    border-radius: 3px;
    transition: all 0.2s ease;
}

.tasks-scrollable::-webkit-scrollbar-thumb:hover {
    background: rgba(139, 92, 246, 0.6);
    transform: scale(1.1);
}

/* Пустое состояние при отсутствии задач */
.empty-state {
    height: 200px;
    display: flex;
    align-items: end;
    justify-content: center;
    color: #666666;
    font-size: 1.1rem;
    text-align: center;
    padding-bottom: 2rem;
}

/* Список задач без стандартных маркеров */
.tasks-list {
    list-style: none;
    padding: 0;
    margin: 0 0 1rem 0;
}

/* Карточка одной задачи - увеличен внутренний padding */
.task-item {
    padding: 2rem 1.5rem 1.5rem;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 1.5rem;
    border: 1px solid rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(20px);
    margin-bottom: 1rem;
    transition: all 0.3s ease;
    flex-shrink: 0;
    opacity: 0;
    transform: translateY(20px);
    animation: slideInTask 0.5s ease forwards;
}

@keyframes slideInTask {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.task-item:hover {
    background: rgba(255, 255, 255, 0.1);
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(139, 92, 246, 0.2);
}

/* Верхняя часть задачи: чекбокс + текст + кнопки */
.task-main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
}

/* Обертка для чекбокса и текста */
.task-checkbox {
    flex: 1;
    display: flex;
    align-items: center;
    gap: 0.75rem;
    cursor: pointer;
}

/* Скрыть стандартный чекбокс */
.task-checkbox input[type="checkbox"] {
    display: none;
}

/* Кастомный круглый чекбокс основной задачи */
.custom-checkbox {
    width: 24px;
    height: 24px;
    border: 2px solid rgba(139, 92, 246, 0.5);
    border-radius: 50%;
    position: relative;
    transition: all 0.3s ease;
    flex-shrink: 0;
}

.custom-checkbox::after {
    content: '';
    position: absolute;
    inset: 0;
    background: rgba(139, 92, 246, 0.8);
    border-radius: 50%;
    transform: scale(0);
    transition: transform 0.3s ease;
}

input:checked + .custom-checkbox::after {
    transform: scale(1);
}

input:checked + .custom-checkbox {
    background: rgba(139, 92, 246, 0.3);
    border-color: #8b5cf6;
}

/* Обертка для контента задачи */
.task-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

/* Текст основной задачи */
.task-text {
    color: #000000;
    font-size: 1.1rem;
    transition: all 0.3s ease;
}

.task-text.done {
    opacity: 0.6;
    text-decoration: line-through;
}

/* XP Progress Bar */
.task-xp-bar {
    width: 100%;
    height: 4px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 2px;
    margin-top: 0.5rem;
    overflow: hidden;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.task-xp-bar.visible {
    opacity: 1;
}

.task-xp-fill {
    height: 100%;
    background: linear-gradient(90deg, #8b5cf6, #a78bfa);
    border-radius: 2px;
    width: 0;
    transition: width 1s ease-out;
    position: relative;
}

.task-xp-fill::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    animation: shimmer 2s infinite;
}

@keyframes shimmer {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(100%); }
}

/* Контейнер для кнопок (подзадачи / удалить) */
.task-actions {
    display: flex;
    gap: 0.5rem;
    align-items: center;
    min-width: 80px;
    justify-content: flex-end;
}

/* Кнопка раскрытия подзадач */
.subtasks-toggle {
    width: 32px;
    height: 32px;
    border: 1px solid rgba(139, 92, 246, 0.5);
    background: rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    color: #8b5cf6;
    font-size: 1.2rem;
    cursor: pointer;
    transition: all 0.3s ease;
}

.subtasks-toggle:hover {
    background: rgba(139, 92, 246, 0.2);
    transform: scale(1.1);
}

/* Кнопка удаления основной задачи */
.delete-btn {
    width: 32px;
    height: 32px;
    border: 1px solid rgba(255, 0, 0, 0.3);
    background: rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    color: #ff6b6b;
    font-size: 1.2rem;
    cursor: pointer;
    transition: all 0.3s ease;
}

.delete-btn:hover {
    background: rgba(255, 0, 0, 0.2);
    transform: scale(1.1);
}

/* Контейнер подзадач */
.subtasks-content {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s cubic-bezier(0.4, 0, 0.2, 1), margin 0.2s ease;
    margin-top: 0;
}

.subtasks-content.expanded {
    max-height: 280px;
    margin-top: 0.75rem;
}

/* Список подзадач */
.subtasks-list {
    list-style: none;
    padding: 0.5rem 0 0.25rem 1.25rem;
    margin: 0;
}

/* Элемент подзадачи */
.subtask-item {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    padding: 0.375rem 0;
    opacity: 0;
    animation: fadeIn 0.2s ease forwards;
}

.subtask-item:nth-child(1) { animation-delay: 0.05s; }
.subtask-item:nth-child(2) { animation-delay: 0.1s; }
.subtask-item:nth-child(3) { animation-delay: 0.15s; }
.subtask-item:nth-child(3) { animation-delay: 0.15s; }

@keyframes fadeIn {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Обертка для чекбокса и текста подзадачи */
.subtask-checkbox {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    cursor: pointer;
    flex: 1;
}

/* Кастомный квадратный чекбокс подзадачи */
.custom-subcheckbox {
    width: 16px;
    height: 16px;
    border: 2px solid rgba(139, 92, 246, 0.4);
    border-radius: 3px;
    position: relative;
    transition: all 0.2s ease;
    flex-shrink: 0;
}

.custom-subcheckbox::after {
    content: '';
    position: absolute;
    inset: 2px;
    background: #8b5cf6;
    transform: scale(0) rotate(-45deg);
    transition: all 0.2s ease;
}

input:checked + .custom-subcheckbox::after {
    transform: scale(1) rotate(-45deg);
}

input:checked + .custom-subcheckbox {
    background: rgba(139, 92, 246, 0.3);
    border-color: #8b5cf6;
}

/* Текст подзадачи */
.subtask-text {
    color: #000000;
    font-size: 0.875rem;
    line-height: 1.3;
}

.subtask-text.done {
    opacity: 0.6;
    text-decoration: line-through;
}

/* Кнопка удаления подзадачи */
.delete-sub-btn {
    width: 24px;
    height: 24px;
    border: none;
    background: rgba(255, 0, 0, 0.15);
    border-radius: 50%;
    color: #ff6b6b;
    font-size: 0.875rem;
    cursor: pointer;
    transition: all 0.2s ease;
    flex-shrink: 0;
    opacity: 0.7;
}

.delete-sub-btn:hover {
    background: rgba(255, 0, 0, 0.3);
    transform: scale(1.05);
    opacity: 1;
}

/* Блок с инпутом и кнопкой для добавления подзадачи */
.add-subtask-section {
    display: flex;
    gap: 0.5rem;
    padding-top: 0.5rem;
    opacity: 0;
    transform: translateY(4px);
    transition: all 0.2s ease;
}

.subtasks-content.expanded .add-subtask-section {
    opacity: 1;
    transform: translateY(0);
}

/* Инпут для новой подзадачи */
.subtask-input {
    flex: 1;
    padding: 0.4375rem 0.75rem;
    background: rgba(255, 255, 255, 0.06);
    border: 1px solid rgba(139, 92, 246, 0.2);
    border-radius: 0.75rem;
    color: #000000;
    font-size: 0.8125rem;
    backdrop-filter: blur(16px);
    transition: all 0.2s ease;
}

.subtask-input::placeholder {
    color: #666666;
}

.subtask-input:focus {
    border-color: rgba(139, 92, 246, 0.5);
    box-shadow: 0 0 0 2px rgba(139, 92, 246, 0.1);
    outline: none;
}

/* Кнопка "Добавить" подзадачу */
.add-sub-btn {
    padding: 0.4375rem 1rem;
    background: rgba(139, 92, 246, 0.7);
    color: white;
    border: none;
    border-radius: 0.75rem;
    font-weight: 600;
    font-size: 0.8125rem;
    cursor: pointer;
    transition: all 0.2s ease;
    white-space: nowrap;
    opacity: 0.9;
}

.add-sub-btn:hover {
    background: #8b5cf6;
    transform: translateY(-1px);
    opacity: 1;
}

/* Нижняя панель для добавления основной задачи */
.add-task-section {
    display: flex;
    gap: 1rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    padding-top: 1rem;
    flex-shrink: 0;
}

/* Инпут основной задачи */
.task-input {
    flex: 1;
    padding: 1.25rem 1.5rem;
    background: rgba(255, 255, 255, 0.08);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 2rem;
    color: #000000;
    font-size: 1.1rem;
    backdrop-filter: blur(20px);
    transition: all 0.3s ease;
}

.task-input::placeholder {
    color: #666666;
}

.task-input:focus {
    outline: none;
    border-color: #8b5cf6;
    box-shadow: 0 0 0 3px rgba(139, 92, 246, 0.2);
}

/* Кнопка добавления основной задачи */
.add-btn {
    padding: 1.25rem 2rem;
    background: linear-gradient(135deg, #8b5cf6, #7c3aed);
    color: white;
    border: none;
    border-radius: 2rem;
    font-weight: 700;
    font-size: 1.1rem;
    cursor: pointer;
    backdrop-filter: blur(20px);
    transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
    white-space: nowrap;
}

.add-btn:hover {
    transform: translateY(-3px);
    box-shadow: 0 16px 40px rgba(139, 92, 246, 0.5);
}

/* Адаптив: планшеты и десктоп */
@media (min-width: 769px) {
    .main-content {
        max-width: 800px;
    }

    .task-item {
        padding: 2rem 1.5rem 1.5rem;
    }
}

/* Адаптив: мобильные устройства */
@media (max-width: 768px) {
    .sphere-page {
        padding: 1.5rem 1rem;
    }

    .header-simple {
        flex-direction: column;
        align-items: flex-start;
        gap: 1.5rem;
    }

    .hero-compact {
        width: 100%;
        justify-content: center;
    }

    .add-task-section,
    .add-subtask-section {
        flex-direction: column;
    }

    .main-content {
        gap: 3rem;
        max-width: 100%;
    }

    .empty-state {
        height: 250px;
    }

    .tasks-container {
        max-height: 60vh;
    }

    .tasks-scrollable::-webkit-scrollbar {
        width: 4px;
    }
}
